          label: "不标颜色",
          value: "0"
        },
        {
          label: "红色",
          value: "1"
        },
        {
          label: "绿色",
          value: "2"
        }
      ],
      editRemark: false,
      value: "",
      pickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          this.times = minDate.getTime();
          if (maxDate) {
            this.times = "";
          }
        },
        disabledDate: time => {
          if (this.times !== "") {
            const one = 14 * 24 * 3600 * 1000;
            const minTime = this.times - one;
            const maxTime = this.times + one;
            return time.getTime() < minTime || time.getTime() > maxTime;
          }
        }
      },
      today_data: [],
      uploadForm: null,
      inputItem: "",
      flag1: false,
      spu1: "",
      spus1: [],
      curItem: "",
      curIndex1: 0,
      curTrIdx: 0,
      A_MIndex: 0,
      A_M_list: ["自动组", "手动组"],
      channels: [],
      selectOpt: [],
      sites: [],
      countries: [],
      form: {
        channel: "all",
        site: "all",
        country: "all"
      },
      overview_head: [
        "营业额",
        "广告花费",
        "广告比",
        "手动组ACOS",
        "自动组ACOS"
      ],
      hjData: [],
      overview_body: [],
      dates: "",
      monitor_head: [
        "序号",
        "广告活动",
        "投放",
        "预算",
        "曝光量",
        "点击次数",
        "花费",
        "每次点击费用",
        "订单",
        "销售额",
        "ACOS",
        "备注"
      ],
      monitor_body: [],
      monitor_datepicker: "",
      monitor_PickerOptions: {
        onPick: ({ maxDate, minDate }) => {
          console.log(maxDate, minDate);
          this.times = minDate.getTime();
          if (maxDate) {
            this.times = "";
          }
        },
        disabledDate: time => {
          if (this.times !== "") {
            const one = 14 * 24 * 3600 * 1000;
            const minTime = this.times - one;
            const maxTime = this.times + one;
            return time.getTime() < minTime || time.getTime() > maxTime;
          }
        }
      },
      // 上传
      file_name: "",
      show_upload: false,
      timeframe: "",
      timeframsObj: [
        { label: "10点", value: "10:00" },
        { label: "14点", value: "14:00" },
        { label: "17点", value: "17:00" },
        { label: "22点", value: "22:00" },
        { label: "24点", value: "24:00" }
      ],
      chartOpt1: {
        legend: {
          data: ["营业额", "广告花费", "广告比", "手动组ACOS", "自动组ACOS"]
        },
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "3%",
