          data: []
        },
        yAxis: {
          type: "value"
        },
        series: []
      }
    };
  },
  methods: {
    monitor_changeDate() {
      if (this.monitor_datepicker === null) return;
      if (this.monitor_datepicker[0] === this.monitor_datepicker[1]) {
        this.dates = this.monitor_datepicker[0];
        this.timeframe2 = "";
        this.isDiableTimeFrame = true;
      } else {
        this.dates =
          this.monitor_datepicker[0] + "_" + this.monitor_datepicker[1] + "T";
        this.isDiableTimeFrame = false;
      }
    },
    monitor_Search() {
      this.A_MIndex = 0;

      if (
        this.form.channel === "all" ||
        this.form.station === "all" ||
        this.form.country === "all" ||
        (this.monitor_datepicker[0] !== this.monitor_datepicker[1] &&
          !this.timeframe2) ||
        !this.monitor_datepicker
      ) {
        alert(
          "请选择渠道、国家、站点及时间查询！选择不同日期必须选择时间节点！"
        );
        return;
      }
      let url = "server/order_management/choose_advertising_shop_data/";
      let formData = new FormData();
      formData.append("channel", this.form.channel);
      formData.append("station", this.form.site);
      formData.append("country", this.form.country);
      formData.append("dates", this.dates + this.timeframe2);
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }

        console.log(res);
        // 待完成！！！！
        this.manual_data = res.data.manual_data;
        this.auto_data = res.data.auto_data;
        this.monitor_body = res.data.advertising_data;
        this.chartOpt2.series = [];
        // x轴
        this.chartOpt2.xAxis.data =
          res.data.auto_data.acos_list.data_labels || [];
        this.initSelectObj();
        this.initSeries(this.chartOpt2, this.auto_data);
        console.log(this.chartOpt2);
        this.initEchart("chart2", this.chartOpt2);
      });
    },
    changeInput(idx) {
      // 编辑的索引
      if (this.changeIndexArr.indexOf(idx) === -1) {
        this.changeIndexArr.push(idx);
      }
      console.log(idx);
    },
    checkAcosColor() {
      // 确定颜色。 做一些操作。

      this.monitor_body[this.curAcosIndex].acos_status = this.acos_color;
      console.log(this.acos_color);
      console.log(this.monitor_body[this.curAcosIndex].acos_status);
      this.showAcosDialog = false;
    },
    acosColor(rowItem, rowIndx) {
      if (!this.editRemark) return;
      if (this.changeIndexArr.indexOf(rowIndx) === -1) {
        this.changeIndexArr.push(rowIndx);
      }
      this.showAcosDialog = true;
      this.curAcosIndex = rowIndx;
      this.acos_color = this.monitor_body[this.curAcosIndex].acos_status;
    },
    initSelectObj() {
      let obj = {};
      obj["曝光量"] = true;
      obj["点击次数"] = true;
      obj["订单"] = true;
      obj["ACOS"] = true;
      obj["点击率"] = false;
      obj["花费"] = false;
      obj["销售额"] = false;
      obj["每次点击费用"] = false;
      this.chartOpt2.legend.selected = obj;
    },
    changeAM(idx) {
      this.A_MIndex = idx;
      this.chartOpt2.series = [];
      if (idx === 0) {
        this.initSeries(this.chartOpt2, this.auto_data);
      } else {
        this.initSeries(this.chartOpt2, this.manual_data);
      }
      this.initEchart("chart2", this.chartOpt2);
    },
    initSeries(obj, dataObj) {
      console.log("数据", dataObj);
      console.log(dataObj.exposure_list.datas);
      obj.series.push({
        name: "曝光量",
        stack: "曝光量",
        type: "line",
        data: dataObj.exposure_list.datas
      });
      obj.series.push({
        name: "点击次数",
        stack: "点击次数",
        type: "line",
        data: dataObj.click_num_list.datas
      });
      obj.series.push({
        name: "点击率",
        stack: "点击率",
        type: "line",
        data: dataObj.click_rate_list.datas
      });
      obj.series.push({
        name: "花费",
        stack: "花费",
        type: "line",
        data: dataObj.cost_list.datas
      });
      obj.series.push({
        name: "每次点击费用",
        stack: "每次点击费用",
        type: "line",
        data: dataObj.click_cost_list.datas
      });
      obj.series.push({
        name: "订单",
        stack: "订单",
        type: "line",
        data: dataObj.order_num_list.datas
      });
      obj.series.push({
        name: "销售额",
        stack: "销售额",
        type: "line",
        data: dataObj.sales_list.datas
      });
      obj.series.push({
        name: "ACOS",
        stack: "ACOS",
        type: "line",
        data: dataObj.acos_list.datas
      });
      console.log(obj);
    },
    editRemarks() {
      // 备注
      this.editRemark = true;
    },
    submitRemarks() {
      this.editRemark = false;
      // 发起请求
      console.log(this.changeIndexArr);
      let arr = [];
      this.changeIndexArr.forEach(i => {
        let obj = {};
        let item = this.monitor_body[i];
        obj["id"] = item["id"];
        obj["acos_status"] = item["acos_status"];
        obj["remarks"] = item["remarks"];
        arr.push(obj);
      });
      let data = JSON.stringify(arr);
      let url = "server/order_management/edit_advertising_shop_data/";
      let formData = new FormData();
      formData.append("data", data);
      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }

        console.log("编辑数据提交", res.data);
        if (res.data.code === 200) {
          alert("提交成功");
        } else {
          alert("提交未成功！");
        }
      });
    },
    onSubmit() {
      if (
        this.uploadForm === null ||
        this.form.store === "all" ||
        this.form.country === "all"
      ) {
        alert("请检查是否缺少国家、站点、文件等数据");
        this.show_upload = false;
        this.timeframe = "";
        return;
      }
      // // 发送请求
      let url = "server/order_management/upload_advertising_shop_data/";
      this.$axios.post(this._url2 + url, this.uploadForm).then(res => {
        this.show_upload = false;
        this.uploadForm = null;
        // console.log(res.data.code);
        if (res.data.code === 403) {
          let coverflag = confirm("该节点数据已上传，是否覆盖");
          if (!coverflag) {
            // 不覆盖
            console.log("不覆盖");
            return;
          } else {
            console.log("覆盖");
            // 覆盖
            this.uploadForm = new FormData();
            let files = this.inputItem.files[0];
            if (files.name) {
              this.file_name = files.name;
            }
            this.uploadForm.append("file", files);
            this.uploadForm.append("channel", this.form.channel);
            this.uploadForm.append("station", this.form.site);
            this.uploadForm.append("country", this.form.country);
            // 新增 times
            this.uploadForm.append("times", this.upload_times);
            this.uploadForm.append("dates", this.timeframe);
            this.uploadForm.append("cover", 1);
            this.$axios.post(this._url2 + url, this.uploadForm).then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }
              this.monitor_body = res.data.data;
            });
          }
        } else if (res.data.code !== 403 && res.data.code !== 200) {
          alert("上传出错，请联系管理员");
        } else {
          //todo
          this.monitor_body = res.data.data;
        }
      });
      // 上传数据
      // this.show_upload = false;
      // // 上传信息
      // this.lastUploadInfo.timeframe = this.timeframe;
    },
    // chart1修改
    changeData() {
      // 发送请求。
      let flag = true;
      for (let key in this.form) {
        if (this.form[key] === "all") {
          flag = false;
        }
      }
      if (!this.spu1) {
        flag = false;
      }
      if (!flag) {
        alert("请检查是否选择渠道、国家、站点、spu及时间");
        return;
      }
      let [start_time, end_time] = this.value;
      console.log(start_time, end_time);
      let url = "server/order_management/get_spu_advertising_data/";
      let formData = new FormData();
      formData.append("channel", this.form.channel);
      formData.append("station", this.form.site);
      formData.append("country", this.form.country);
      formData.append("spu", this.spu1);
      formData.append("start_time", start_time);
      formData.append("end_time", end_time);

      this.$axios.post(this._url2 + url, formData).then(res => {
        if (res.data.code !== 200) {
          alert("网页出错，请联系IT管理员！");
          return;
        }

        this.curIndex1 = 0;
        this.chartOpt1.xAxis.data = res.data.date_list;
        console.log(this.chartOpt1);
        this.curItem = res.data.data;
        // 最大值
        console.log(this.curItem.days_cost_rate_data);
        let array = this.curItem.days_cost_rate_data.map(i => {
          console.log(Number(i));
          return i === Number(i) ? i : i.split("%")[0];
        });
        this.chartOpt1.series = [
          {
            name: "营业额",
            stack: "营业额",
            type: "line",
            data: this.curItem.days_sales_data
          },
          {
            name: "广告花费",
            stack: "广告花费",
            type: "line",
            data: this.curItem.days_advertising_costs_data
          },
          {
            name: "广告比",
            stack: "广告比",
            type: "line",
            data: array
          },
          {
            name: "手动组ACOS",
            stack: "手动组ACOS",
            type: "line",
            data: this.curItem.days_manual_acos_data
          },
          {
            name: "自动组ACOS",
            stack: "自动组ACOS",
            type: "line",
            data: this.curItem.days_auto_acos_data
          }
        ];
        this.initEchart("chart1", this.chartOpt1);
      });
    },
    // changeLine(val) {
    //   this.curIndex1 = val;
    //   switch (val) {
    //     case 0:
    //       // 修改series
    //       this.chartOpt1.series = [
    //         {
    //           name: "营业额",
    //           stack: "营业额",
    //           type: "line",
    //           data: this.curItem.days_sales_data
    //         }
    //       ];
    //       break;
    //     case 1:
    //       this.chartOpt1.series = [
    //         {
    //           name: "广告花费",
    //           stack: "广告花费",
    //           type: "line",
    //           data: this.curItem.days_advertising_costs_data
    //         }
    //       ];
    //       break;
    //     case 2:
    //       let array = this.curItem.days_cost_rate_data.map(i => {
    //         return i === 0 ? 0 : i.split("%")[0];
    //       });
    //       console.log(array);
    //       this.chartOpt1.series = [
    //         {
    //           name: "广告比",
    //           stack: "广告比",
    //           type: "line",
    //           data: array
    //         }
    //       ];
    //       break;
    //     case 3:
    //       this.chartOpt1.series = [
    //         {
    //           name: "手动组ACOS",
    //           stack: "手动组ACOS",
    //           type: "line",
    //           data: this.curItem.days_manual_acos_data
    //         }
    //       ];
    //       break;
    //     case 4:
    //       this.chartOpt1.series = [
    //         {
    //           name: "自动组ACOS",
    //           stack: "自动组ACOS",
    //           type: "line",
    //           data: this.curItem.days_auto_acos_data
    //         }
    //       ];
    //       break;
    //   }
    //   this.initEchart("chart1", this.chartOpt1);
    // },
    changeOpt(val) {
      this.A_MIndex = 0;
      this.curTrIdx = 0;
      // this.overview_body = [];
      if (val === "country") {
        this.form.site = "all";
        this.sites = [];
        if (this.form.country !== "all") {
          let url = this._url2 + "server/personnel_management/get_sign_area/";
          // let url =
          //   "http://106.52.43.196:8003/server/personnel_management/get_sign_area/";

          this.$axios
            .get(url, {
              params: {
                country: this.form.country
              }
            })
            .then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }
              this.sites = res.data.data;
            });
        }
      } else if (val === "store") {
        if (
          this.form.channel !== "all" &&
          this.form.site !== "all" &&
          this.form.country !== "all"
        ) {
          let url = "server/order_management/advertising_shop/";
          this.$axios
            .get(this._url2 + url, {
              params: {
                station: this.form.site,
                country: this.form.country
              }
            })
            .then(res => {
              if (res.data.code !== 200) {
                alert("网页出错，请联系IT管理员！");
                return;
              }
              console.log("初始数据", res.data);
              // 监控管理
              this.monitor_body = res.data.advertising_data;
              this.auto_data = res.data.auto_data;
              this.date_list = res.data.date_list;
              this.manual_data = res.data.manual_data;
              console.log(this.auto_data);
              this.today_data = res.data.today_data;
              this.chartOpt1.xAxis.data = res.data.date_list || [];
              this.chartOpt1.series = [];
              if (this.today_data.length) {
                this.flag1 = true;
                this.hjData = this.today_data[0] || [];
                if (this.today_data.length > 1) {
                  this.curItem = this.today_data[1];
                  this.spu1 = this.today_data[1].spu;
                  let array = this.curItem.days_cost_rate_data.map(i => {
                    return i === Number(i) ? i : i.split("%")[0];
                  });
                  this.chartOpt1.series = [
                    {
                      name: "营业额",
                      stack: "营业额",
                      type: "line",
                      data: this.curItem.days_sales_data
                    },
                    {
                      name: "广告花费",
                      stack: "广告花费",
                      type: "line",
                      data: this.curItem.days_advertising_costs_data
                    },
                    {
                      name: "广告比",
                      stack: "广告比",
                      type: "line",
                      data: array
                    },
                    {
                      name: "手动组ACOS",
                      stack: "手动组ACOS",
                      type: "line",
                      data: this.curItem.days_manual_acos_data
                    },
                    {
                      name: "自动组ACOS",
                      stack: "自动组ACOS",
                      type: "line",
                      data: this.curItem.days_auto_acos_data
                    }
                  ];
                  this.overview_body = this.today_data.slice(1) || [];
                  this.overview_body.forEach(i => {
                    if (i !== 0) {
                      this.spus1.push(i.spu);
                    }
                  });
                }
              }
              this.initEchart("chart1", this.chartOpt1);
              // 图表2
              this.chartOpt2.series = [];
              this.chartOpt2.xAxis.data =
                res.data.auto_data.acos_list.data_labels;
              this.initSelectObj();
              this.initSeries(this.chartOpt2, this.auto_data);
              console.log(this.chartOpt2);
              this.initEchart("chart2", this.chartOpt2);
            });
        }
      }
    },
    showDialog() {
      this.show_upload = true;
    },
    changeFile() {
      this.uploadForm = new FormData();
      let files = this.$refs.uploadinput.files[0];
      this.inputItem = this.$refs.uploadinput;
      this.file_name = files.name;
      this.uploadForm.append("file", files);
      this.uploadForm.append("channel", this.form.channel);
      this.uploadForm.append("station", this.form.site);
      this.uploadForm.append("country", this.form.country);
      // 新增 times
      this.uploadForm.append("times", this.upload_times);

      this.uploadForm.append("dates", this.timeframe);
    },
    showSpu1(spu, idx) {
      this.spu1 = spu;
      this.curTrIdx = idx;
      this.curIndex1 = 0;
      this.value = null;
      this.chartOpt1.xAxis.data = this.date_list;
      this.overview_body.forEach(i => {
        if (i.spu === spu) {
          console.log(this.curItem);
          this.curItem = i;
          let array = this.curItem.days_cost_rate_data.map(i => {
            return i === Number(i) ? i : i.split("%")[0];
          });
          this.chartOpt1.series = [
            {
              name: "营业额",
              stack: "营业额",
              type: "line",
              data: this.curItem.days_sales_data
            },
            {
              name: "广告花费",
              stack: "广告花费",
              type: "line",
              data: this.curItem.days_advertising_costs_data
            },
            {
              name: "广告比",
              stack: "广告比",
              type: "line",
              data: array
            },
            {
              name: "手动组ACOS",
              stack: "手动组ACOS",
              type: "line",
              data: this.curItem.days_manual_acos_data
            },
            {
              name: "自动组ACOS",
              stack: "自动组ACOS",
              type: "line",
              data: this.curItem.days_auto_acos_data
            }
          ];
          this.initEchart("chart1", this.chartOpt1);
        }
      });
    },
    // 初始化echart
    initEchart(chart, option) {
      // chart1
      const chart_ = this.$refs[chart];
      if (chart_) {
        this["my_" + chart] = this.$echarts.init(chart_);
        this["my_" + chart].setOption(option, true);
        window.addEventListener("resize", () => {
          this["my_" + chart].resize();
        });
      }
      this.$on("hook:destoryed", () => {
        window.removeEventListener("resize", () => {
          this["my_" + chart].resize();
        });
      });
    },
    // 清空echart图标
    clearEchart(chartObj, chartopt) {
      chartopt = {
        title: {
          text: ""
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: []
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: []
        },
        yAxis: {
          type: "value"
        },
        series: []
      };
      chartObj.setOption(chartopt, true);
    },
    dateFormat() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      this.upload_times = year + "-" + month + "-" + day;
    }
  },
  mounted() {
    this.initEchart("chart1", this.chartOpt1);
    this.initEchart("spuChart", this.spuChartOpt);
    this.initEchart("chart2", this.chartOpt2);
  },
  created() {
    this.dateFormat();
    // let url = "server/order_management/advertising_shop_country/";
    // this._url2 = "http://106.52.43.196:8003/";
    let url = "server/personnel_management/get_sign_country/";
    // console.log(this._url2);
    this.$axios.get(this._url2 + url).then(res => {
      if (res.data.code !== 200) {
        alert("网页出错，请联系IT管理员！");
        return;
      }
      this.channels.push(res.data.data1);
      console.log(res.data.data);
      this.countries = res.data.data;
    });
  }
};
</script>
